.contactsFilter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  box-shadow: rgba(0, 0, 0, 0.176) 0px 16px 48px 0px;
  background: rgba(0, 0, 0, 0);
  appearance: none;
  border: 0;
  border-radius: 8px;
  width: 280px;
  position: relative;
  line-height: 1.4;
  color: rgb(0, 0, 0);
  font-size: 16px;
  font-weight: 400;
  height: 26px;
  transition: all 0.2s ease;
  padding: 9px 12px;
  margin-top: 26px;
}

.input :hover {
  box-shadow: 0 0 0 0 #fff inset, #547cb7 0 0 0 2px;
}

.input:focus {
  background: rgb(129, 158, 194);
  outline: 0;
  box-shadow: 0 0 0 0 rgb(157, 173, 201) inset, #547cb7 0 0 0 3px;
}
